<template>
  <div class="HG-reserve p-p-3 p-p-lg-4 p-text-left">
    <Card style="width: 30em; max-width: 98%" class="p-mx-auto">
        <template #header>
            <img alt="user header" src="../assets/restaurant.jpg" height="200">
        </template>
        <template #title class="p-text-normal">
            Online Reservations
        </template>
        <template #content>
          <form @submit="onSubmit">
            <div class="p-fluid p-formgrid p-grid">
              <div class="p-field p-col-12 p-md-6">
                <div class="p-inputgroup">
                  <span class="p-inputgroup-addon">
                    <i class="pi pi-calendar"></i>
                  </span>
                  <Calendar placeholder="Select date" v-model="reservation.date" :minDate="minDate" :touchUI="true" dateFormat="dd-mm-yy" required />                
                </div>
              </div>
              <div class="p-field p-col-12 p-md-6">
                <div class="p-inputgroup">
                  <span class="p-inputgroup-addon">
                    <i class="pi pi-clock"></i>
                  </span>
                  <Calendar placeholder="Select time" v-model="reservation.time" :timeOnly="true" hourFormat="12" required />
                </div>
              </div>
              <div class="p-field p-col-12 p-md-4 p-pt-3">
                <div class="p-inputgroup">
                  <span class="p-inputgroup-addon">
                    <i class="pi pi-users"></i>
                  </span>
                  <span class="p-float-label">
                      <InputNumber id="people" v-model="reservation.total" :min="1" :max="100" />
                      <label for="people"># of people</label>
                  </span>
                </div>
              </div>
              <div class="p-field p-col-12 p-md-8  p-pt-3">
                <div class="p-inputgroup">
                  <span class="p-inputgroup-addon">
                    <i class="pi pi-map-marker"></i>
                  </span>
                  <Dropdown style="width: 100%" id="location" v-model="reservation.location" :options="optLocation" optionLabel="text" optionValue="code" placeholder="Select a City"  />
                </div>
              </div>
              <div class="p-field p-col-12 p-mt-3">
                <div class="p-inputgroup">
                  <span class="p-inputgroup-addon">
                    <i class="pi pi-id-card"></i>
                  </span>
                  <span class="p-float-label">
                    <InputText style="width: 100%" id="name" v-model="reservation.name" required />
                    <label for="name">Name</label>
                  </span>
                </div>
              </div>
              <div class="p-field p-col-12 p-md-7  p-pt-3">
                <div class="p-inputgroup">
                  <span class="p-inputgroup-addon">
                    <i class="pi pi-envelope"></i>
                  </span>
                  <span class="p-float-label">
                    <InputText type="email" style="width: 100%" id="email" v-model="reservation.email" required />
                    <label for="email">Email</label>
                  </span>
                </div>
              </div>
              <div class="p-field p-col-12 p-md-5 p-pt-3">
                <div class="p-inputgroup">
                  <span class="p-inputgroup-addon">
                    +62
                  </span>
                  <span class="p-float-label">
                    <InputText type="number" style="width: 100%" id="phone" v-model="reservation.phone" required />
                    <label for="phone">Phone</label>
                  </span>
                </div>
              </div>
            </div>
            <p><Button type="submit" icon="pi pi-check" label="Submit" class="p-mr-2" /><Button @click="onReset" icon="pi pi-replay" class="p-button-danger" label="Reset" /></p>
            <div><Button icon="pi pi-arrow-left" label="Back" class="p-button-sm p-button-text" @click="goHome" /></div>
          </form>          
        </template>
    </Card>
    <Dialog header="Konfirmasi Reservasi" :visible.sync="dispConfirmation" :style="{width: '35em', maxWidth: '90%'}">
      <div class="p-grid">
        <div class="p-col-4 p-lg-3 p-text-right">
          When:
        </div>
        <div class="p-col-8 p-lg-9">
          <span class="p-text-bold">{{formatedDate}}</span>
        </div>
        <div class="p-col-4 p-lg-3 p-text-right">
          Total people:
        </div>
        <div class="p-col-8 p-lg-9">
          {{reservation.total}}
        </div>
        <div class="p-col-4 p-lg-3 p-text-right">
          Location:
        </div>
        <div class="p-col-8 p-lg-9">
          {{reservation.location}}
        </div>
        <div class="p-col-4 p-lg-3 p-text-right">
          Name:
        </div>
        <div class="p-col-8 p-lg-9">
          {{reservation.name}}
        </div>
        <div class="p-col-4 p-lg-3 p-text-right">
          Email:
        </div>
        <div class="p-col-8 p-lg-9">
          {{reservation.email}}
        </div>
        <div class="p-col-4 p-lg-3 p-text-right">
          Phone:
        </div>
        <div class="p-col-8 p-lg-9">
          {{reservation.phone}}
        </div>
      </div>
      
      <template #footer>
          <Button label="No" icon="pi pi-times" @click="closeConfirm"/>
          <Button label="Yes" icon="pi pi-check" @click="confirm" autofocus />
          <div></div>
      </template>
    </Dialog>
    <Toast />
  </div>
</template>

<script>
import image from "../assets/restaurant.jpg"
import Button from 'primevue/button';
import Card from 'primevue/card';
import Calendar from 'primevue/calendar';
import InputNumber from 'primevue/inputnumber';
import Dropdown from 'primevue/dropdown';
import InputText from 'primevue/inputtext';
import Dialog from 'primevue/dialog';
import Toast from 'primevue/toast';


export default {
  name: 'HG',
  components: {
    Button, Card, Calendar, InputNumber, Dropdown, InputText, Dialog, Toast
  },
  data() {
    return {
      reservation: {},
      formatedDate: null,
      optLocation: [
        { code: "jakarta", text: "Jakarta" },
        { code: "tangerang", text: "Tangerang" }
      ],
      reserveImage: image,
      minDate: "",
      dispConfirmation: false
    }
  },
  created() {
    this.minDate = new Date()    
  },
  mounted() {
    this.onReset()
  },
  methods: {
    onSubmit(event) {
      event.preventDefault()
      const dayName = ["Sun", "Mon", "Tue", "Wed", "Thu", "Fri", "Sat"]
      const inDate = new Date(this.reservation.date)
      var formatedMonth = (inDate.getMonth() + 1) + ""
      var formatedDate = inDate.getDate() + ""
      this.formatedDate = dayName[inDate.getDay()] + ", " + formatedDate.padStart(2, "0") + "-" + formatedMonth.padStart(2,"0") + "-" + inDate.getFullYear()
      this.formatedDate += " at " + (this.reservation.time.getHours()+"").padStart(2,"0") + ":" + (this.reservation.time.getMinutes()+"").padStart(2,"0")
      this.dispConfirmation = true
    },
    onReset() {
      const today = new Date()
      today.setHours(10)
      today.setMinutes(0)
      const initialData = {
        date: "",
        time: today,
        name: "",
        email: "",
        phone: "",
        total: 1,
        location: null
      }
      this.reservation = initialData
    },
    confirm() {
      this.closeConfirm()
      this.$toast.add({severity:'success', summary: 'Reservation Success', detail:'Your reservation has been successfully sent', life: 5000});
      console.log(this.reservation.date.getDate())
    },
    closeConfirm() {
      this.dispConfirmation = false
    },
    goHome() {
      this.$router.push({path: "/"})
    }
  }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.HG-reserve {
  background-image: url("../assets/hachigrill.jpg");
  background-size: cover;
  min-height: 100vh
}
</style>

<style>
div.p-card-title {
  font-weight: 500 !important;
}
</style> 